@import url('https://fonts.googleapis.com/css2?family=Rubik&display=swap');

:root {
  --primary-orange: #f9ab00;
}

body{
  font-family: 'Rubik', sans-serif !important;
  
}
/* navbar */
nav{
  z-index: 2;
}
#name{
  color:orange;
  font-size: 1.5rem;
cursor: pointer;
}


#home, #experience, #about, #port{
  font-weight: 100;
  color:white;
  cursor: pointer;
}
.navbar-nav >  li >  a.active, .navbar-nav >  li >  a:focus {
  border-top: 0.16rem solid var(--primary-orange);
}
.navbar-nav >  li >  a:hover {
  border-top: 0.16rem solid var(--primary-orange);
}
/* Header */

.header-wrapper {
  position: relative;
  background: url(web-dev.jpeg) no-repeat;
  background-size: cover;
  background-position: center;
  height: 100vh;
}
.main-info {
  display: flex;
  position: absolute;
  top: 0;
  left: 0;
  right: 0;
  bottom: 0;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  text-align: center;
  font-family: 'Rubik', sans-serif;
  z-index: 1;
}

.main-info h1{
  color: white;
}
@media(max-width: 768px) {
    .main-info h1{
      font-size: 1.5rem !important;
    }

  }

.typed-text{
  color:#fb8500;
  font-size: 2rem;
}

.btn-main-offer {
  border: 0.0625rem solid;
  color: white;
  
  border-radius: 0;
  padding: 0.625rem  0.75rem 0.75rem 0.75rem;
  margin: 2rem;
  }

  .tsparticles-canvas-el{
    position: absolute;
    height: 100vh;
    width: 100vw;
    z-index: 1;

  }

  .btn-main-offer:hover {
    text-decoration:none;
    background-color: #023047; 
    color:white;
    transition: .2s ease-in-out;
    box-shadow: 0.3125rem 0.3125rem  orange;
  }

  /* @media(max-width: 768px) {
    .tsparticles-canvas

  } */
  /* about me */
  .aboutMe {
    background: #e5e5e5;
  }
  .photo-wrap {
    display: flex;
    justify-content: center;
    align-items: center;
  }
  .info-wrap {
    display: flex;
    justify-content: center;
    align-items: center;
  }
.profile-img {
  width: 16rem;
  border-radius: 50%;
  padding: .3rem;
  border: 1px solid #fca311;
}
#about-heading {
  color: #023047;
}

@media(max-width: 468px) {
  .profile-img {
    width: 10rem;


  }
}

/* skills */

.skills {
text-align: center;
padding-bottom: 2rem;
background: #8ecae6; 
}
.skills h1{
  color:#d00000;
}

.skills .circleOne {
  position: relative;
  margin: 0.375rem auto;
  background: #ffba08;
  border-radius: 50%;
  width: 6rem;
  height: 6rem;
}
.skills .circleTwo {
  position: relative;
  margin: 0.375rem auto;
  background: #faa307;
  border-radius: 50%;
  width: 6rem;
  height: 6rem;
}
.skills .circleThree {
  position: relative;
  margin: 0.375rem auto;
  background: #f48c06;
  border-radius: 50%;
  width: 6rem;
  height: 6rem;
}
.skills .circleFour {
  position: relative;
  margin: 0.375rem auto;
  background: #e85d04;
  border-radius: 50%;
  width: 6rem;
  height: 6rem;
}

.skills .iconOne {
  color: #e85d04;
  position: absolute;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%)
}
.skills .iconTwo {
  color: #dc2f02;
  position: absolute;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%)
}
.skills .iconThree {
  color: #9d0208;
  position: absolute;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%)
}
.skills .iconFour {
  color: #ffba08;
  position: absolute;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%)
}
.skills h3{
  color:#370617;
}

.skills .box{
  height: 16rem;
  border-bottom: 5px solid #d00000;
  padding: 0.625rem;
  margin-bottom: 1.875;
  background:#f8f9fa;
  transition: 0.3s ease-in-out;
}

.skills .box:hover {
  background: black;
}

.skills h3:hover {
  color:#f8f9fa;
}

/* experience */

.experience-wrapper {
  position: absolute;
  padding: 3.125rem;
  overflow: hidden;
}
.experience h1 {
  color: #012a4a;
}
.experience-wrapper:before {
  position: absolute;
  content: "";
  top: 0;
  left: 50%;
  margin-left: -0.0625rem;
  width: 0.125rem;
  height: 100%;
  background: #2c7da0;

}

/* portfolio */

.portfolio-wrapper {
  background:#fb8500;
  padding-bottom: 2rem ;
}
.portfolio-wrapper {
  color:  #6a040f;
}

.portfolio-image {
  width: 15rem;
  border: 2px solid #6a040f;
  padding: 0 0.5rem;
}
.portfolio-image-box {
 position: relative;
 margin: 1rem;
}

.overflow {
  position: absolute;
  opacity: 0;
  background: #6a040f;
  width: 15rem;
  height: 8rem; 
  top: 0;
  border: 2px solid white;
  cursor: pointer;
}
.portfolio-image-box:hover .overflow {
  opacity: 60%;
  transition: 0.3s ease-in-out;
  }

  .portfolio-icon {
    position: absolute;
    opacity: 0;
    margin-left: -8rem;
    margin-top: 3.6rem;
    color: #98c1d9;
    font-size: 1.5rem;
  }
.portfolio-image-box:hover .portfolio-icon {
opacity: 1;
transition: 0.3s ease-in-out;
cursor: pointer;
}

.portfolio-image-popupbox {
  width: 45rem;
  padding: 0 0.5rem
}

.hyper-link {
  cursor: pointer;
  color: #e85d04;
}
.hyper-link:hover {
  
  color: green;
}
@media(max-width: 768px) {
.portfolio-image-popupbox {
  width:100%
}
}
@media(max-height: 570px) {
  .popupbox-wrapper {
    height: 100%
  }
  .portfolio-image-popupbox {
    width:50%
  }
  }
@media(max-height: 280px) {
  .popupbox-wrapper {
    height: 90%
  }
  .portfolio-image-popupbox {
    width:30%
  }
  }

  /* Contact */

  .contacts {
    background: #8b8c89;
    padding-bottom: 2rem ;
  }

  .contacts h1 {
    color: #ffc300; 
  }

  .contacts p {
    color: #023578;
    font-size: 1.2rem;
  }

  .contacts input, .contacts textarea {
width: 100%;
box-sizing: border-box;
background: none;
resize: none;
border: 0;
border-bottom: 0.125rem solid   black;
border-radius: 0;
color:#023578;
outline: 0 !important;
box-shadow: none !important;
margin-bottom: 1rem;
}

.contacts .container {
  padding: 4rem 0;
}
.contacts input:focus, .contacts textarea:focus {
  background: transparent;
  color:#ffc300;
  /* border-bottom: 0.125rem solid   black; */
}

.contacts textarea {
  height: 12rem !important;
  
}
.contact-btn {
  margin: 1rem 0 0;
  width:100%;
  color: #023047;
  background: none;
}

.line{
  position: relative;
  top: -0.8rem;
  width: 0;
  height: 0.125rem;
  background: #ffc300;
  display: inline-block;
}

input:focus+.line, textarea:focus+.line {
  width: 100%;
  transition: width 0.5s ease-in-out;
}

input.form-control {
  margin: -0.3rem 0;
}
textarea.form-control {
  margin: -0.3rem 0;
}

@media(max-width: 768px) {
  
  .contacts {
    overflow-x: hidden;
  }
  .contacts .container {
    padding: 0 1rem;
  }


.contacts p {
  padding: 0 1rem;

}
}

.error-message {
  color: white;
  background: red;
  text-align: left;

}
.success-message{
  font-weight: 900;
  color: white;
  justify-content: center;
  background:green;
}

/* Footer */

.footer{
  background: black;
  color: slategray;
  padding: 2rem;
}
.footer a {
  text-decoration: none;
  color: slategray;
}

.footer a:hover {
  text-decoration: none;
  color: #4cc9f0;
  cursor: pointer;
}

.footer p{
  margin-bottom: 0;
}


